:root {
  --color-yellow: #EBFF00;
  --color-purple: #FF70D9;
  --color-white: white;
  --color-black: black;
  --fs-xs: 14px;
  --fs-sm: 16px;
  --fs-md: 18px;
  --fs-lg: 20px;
  --fs-xl: 22px;
  --space-sm: 20px;
  --space-md: 30px;
  --space-lg: 50px;
  --space-xl: 80px;
}

* {
  font-family: "Lato", serif;
  padding: 0;
  margin: 0;
}

#root {
  background: linear-gradient(-179.043deg, #0D1E48 0%, #304571 100%);
  color: var(--color-white);
}

/*
  UI
*/

.btn {
  text-decoration: none;
  border-radius: 100px;
  padding: 7px 40px;
  font-weight: bold;
  display: inline-block;
  transition: all 1s ease;
}

.btn:hover {
  opacity: 60%;
}

.btn--primary {
  background-color: var(--color-yellow);
  color: var(--color-black);
}

.btn--outline {
  background-color: transparent;
  color: var(--color-yellow);
  border: 2px solid var(--color-yellow);
}

.text--primary {
  color: var(--color-purple);
}

.text--secondary {
  color: var(--color-yellow);
}

.link {
  color: var(--color-yellow);
  cursor: pointer;
  transition: all 1s ease;
}

.link:hover {
  opacity: 60%;
}

/*
  Nav
*/

.navbar {
  background-color: var(--color-black);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: var(--color-white);
  position: relative;
  z-index: 1000;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .navbar {
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: var(--space-md);
    padding-right: var(--space-md);
    flex-wrap: nowrap;
  }
}

@media screen and (min-width: 1248px) {
  .navbar {
    padding-left: var(--space-xl);
    padding-right: var(--space-xl);
  }
}

.navbar__logo {
  background-image: url('./assets/logo.svg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 26px;
  width: 71.1px;
  margin: 5px 0 5px var(--space-sm);
}

.navbar__menu {
  flex-basis: 100%;
  flex-grow: 1;
  display: none;
  flex-direction: column;
  position: absolute;
  top: 48px;
  left: 0;
  width: 100%;
  background: black;
}

@media screen and (min-width: 768px) {
  .navbar__menu {
    position: initial;
    display: flex;
    align-items: center;
    flex-basis: auto;
    flex-direction: row;
    flex-grow: 0;
    gap: 45px;
    padding: 0;
    width: auto;
  }
}

.navbar__menu--show {
  display: flex;
  transition: all 1s ease;
}

.navbar__menu li {
  width: 100%;
  font-size: var(--fs-md);
  font-weight: bold;
  list-style: none;
  padding: 14px 0;
  display: flex;
  justify-content: center;
  border-top: 1px solid var(--color-yellow);
  box-sizing: border-box;
}

@media screen and (min-width: 768px) {
  .navbar__menu li {
    width: 100%;
    padding: 14px 0;
    display: initial;
    border-top: none;
    box-sizing: border-box;
  }
}

.navbar__menu li a {
  color: var(--color-white);
  text-decoration: none;
}

.navbar__menu li a:hover {
  background: linear-gradient(98.25deg, var(--color-yellow) 0%, var(--color-purple) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.icon-cart {
  height: 20px;
  width: 24px;
  background-image: url('./assets/icons/cart white.svg');
  background-position: center;
  outline: none;
  border: none;
  stroke: none;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s ease;
}

@media screen and (min-width: 768px) {
  .icon-cart {
    height: 30px;
    width: 30px;
  }
}

.icon-cart:hover {
  background-image: url('./assets/icons/cart colored.svg');
}


.icon-burger {
  background-image: url('./assets/icons/menu closed.svg');
  background-position: center;
  height: 38px;
  width: 32px;
  outline: none;
  border: none;
  stroke: none;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}

.icon-cross {
  background-image: url('./assets/icons/menu\ opened.svg');
  background-position: center;
  height: 38px;
  width: 32px;
  outline: none;
  border: none;
  stroke: none;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}

.navbar__icon_burger {
  margin: 5px var(--space-sm) 5px 0;
}

@media screen and (min-width: 768px) {
  .navbar__icon_burger {
    display: none;
  }
}

/*
  Banner
*/

.planets-composition {
  /* border: 1px solid #000; */
  background:
    /* rocket */
    url('./assets/planetsComposition/Vectary texture.svg') 64.47px 226.32px,

    /* brown planet */
    url('./assets/planetsComposition/Ellipse 202.svg') left 206.85px,

    /* white planet */
    url('./assets/planetsComposition/Ellipse 204.svg') 71.08px 284.3px,

    /* yellow gradient planet */
    url('./assets/planetsComposition/Ellipse 194.png') 5.47px 64.7px,

    /* earth planet */
    url('./assets/planetsComposition/Group 9.svg') 32.72px 0px,
    /* url('310-3107131_earth-map-3d-png 2.png') 32.72px 0px, */

    /* white orbit */
    url('./assets/planetsComposition/Ellipse 206.svg') 0 17.31px;

  background-repeat: no-repeat;

  /* +10px for shadow */
  width: 337.13px;
  /* +10px for shadow */
  height: 377px;
}

.banner {
  background-size: cover;
  position: relative;
  z-index: 0;
  background-repeat: no-repeat;
  background-position: center bottom;
  height: 451px;
  display: flex;
  align-items: center;
  justify-content: end;
  text-align: center;
  flex-direction: column-reverse;
  transition: all 1s ease;
}

@media screen and (min-width: 768px) {
  .banner {
    height: 420px;
    justify-content: center;
    padding-left: var(--space-md);
  }
}

@media screen and (min-width: 1248px) {
  .banner {
    height: 490px;
    padding-left: var(--space-xl);
    padding-right: var(--space-xl);
  }
}

.banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0%) 0%, rgba(15, 20, 32, 79%) 100%);
  z-index: 1;
}

.banner__container {
  z-index: 2;
  padding-bottom: 44px;
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  overflow: hidden;
  transition: all 1s ease;
}

@media screen and (min-width: 768px) {
  .banner__container {
    padding-bottom: 0;
    flex-direction: row;
    align-items: center;
    gap: 40px;
    overflow: hidden;
  }
}

@media screen and (min-width: 768px) {
  .banner__texts {
    flex-basis: 60%;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: center;
  }
}

.banner__illustration {
  z-index: 0;
  background-size: cover;
  background-repeat: no-repeat;
  position: initial;
  flex-grow: 1;
  flex-shrink: 1;
  order: 1;
}

@media screen and (min-width: 768px) {
  .banner__illustration {
    position: initial;
    flex-grow: 1;
    flex-shrink: 1;
    order: 1;
  }
}

.banner__title {
  text-align: center;
  font-size: 36px;
  font-weight: 900;
  margin-bottom: 17px;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .banner__title {
    font-size: 48px;
    text-align: left;
  }
}

@media screen and (min-width: 1248px) {
  .banner__title {
    font-size: 72px;
  }
}

.banner__text {
  text-align: center;
  font-size: var(--fs-sm);
  font-weight: 700;
  margin-bottom: 17px;
}

@media screen and (min-width: 768px) {
  .banner__text {
    font-size: var(--fs-xl);
    text-align: left;
  }
}

@media screen and (min-width: 1248px) {
  .banner__text {
    font-size: 32px;
  }
}

.banner__button {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .banner__button a {
    font-size: var(--fs-xl);
    padding: 14px 40px;
  }
}

/*
  Offers
*/

.offers {
  margin-top: var(--space-lg);
  padding-left: var(--space-sm);
  padding-right: var(--space-sm);
  transition: all 1s ease;
}

@media screen and (min-width: 768px) {
  .offers {
    padding-left: var(--space-md);
    padding-right: var(--space-md);
  }
}

@media screen and (min-width: 1248px) {
  .offers {
    padding-left: var(--space-xl);
    padding-right: var(--space-xl);
  }
}

.offers__title {
  font-size: var(--fs-md);
  margin-bottom: var(--space-md);
}

@media screen and (min-width: 768px) {
  .offers__title {
    font-size: 24px;
    font-weight: 900;
  }
}

@media screen and (min-width: 1248px) {
  .offers__title {
    font-size: 32px;
    font-weight: 900;
  }
}

.offers__items {
  gap: var(--space-md);
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .offers__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.offer {
  background-color: var(--color-black);
  border-radius: 10px;
  min-height: 350px;
  display: flex;
  flex-direction: column-reverse;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 1s ease;
}

@media screen and (min-width: 1248px) {
  .offer:first-child {
    grid-column: 1 / -1;
  }

  .offer:last-child {
    grid-column: 1 / -1;
  }
}

.offer__content {
  padding: var(--space-md);
  border-radius: 10px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0%) 0%, rgba(15, 20, 32, 100%) 100%);
  overflow: scroll;
}

.offer__title {
  font-size: 40px;
  line-height: 1;
  margin-bottom: 22px;
  font-weight: 900;
  width: 70%;
}

@media screen and (min-width: 1248px) {
  .offer__title {
    width: 65%;
  }
}

@media screen and (min-width: 1248px) {
  .offer .offer__title {
    font-size: 46px;
  }

  .offer:first-child .offer__title,
  .offer:last-child .offer__title {
    font-size: 74px;
  }
}

.offer__text {
  margin-bottom: 22px;
  font-size: var(--fs-sm);
}

@media screen and (min-width: 1248px) {
  .offer__text {
    font-size: 26px;
    font-weight: 900;
  }
}

@media screen and (min-width: 1248px) {
  .offer__banner {
    font-size: var(--fs-xl);
  }
}

/*
  Info
*/

.info {
  padding-left: var(--space-sm);
  padding-right: var(--space-sm);
  margin: var(--space-lg) 0;
}

@media screen and (min-width: 768px) {
  .info {
    padding-left: var(--space-md);
    padding-right: var(--space-md);
  }
}

@media screen and (min-width: 1248px) {
  .info {
    padding-left: var(--space-xl);
    padding-right: var(--space-xl);
  }
}

.info__title {
  margin-bottom: var(--space-sm);
  font-size: var(--fs-md);
}


@media screen and (min-width: 768px) {
  .info__title {
    font-size: 24px;
  }
}

@media screen and (min-width: 1248px) {
  .info__title {
    font-size: 32px;
  }
}

.info__text {
  opacity: 60%;
  margin-bottom: var(--space-sm);
  font-size: var(--fs-sm);
}

@media screen and (min-width: 768px) {
  .info__text {
    font-size: var(--fs-sm);
  }
}

@media screen and (min-width: 1248px) {
  .info__text {
    font-size: var(--fs-lg);
  }
}

.info__more {
  font-weight: 800;
  font-size: var(--fs-xs);
}

@media screen and (min-width: 1248px) {
  .info__more {
    font-size: var(--fs-lg);
    font-weight: 900;
  }
}

footer {
  padding: var(--space-sm);
  background: var(--color-black);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*
  Footer
*/

.footer__text {
  font-size: var(--fs-md);
  text-align: center;
}

.footer__illustration {
  background: url('./assets/planetsComposition/Vectary\ texture.svg');
  width: 51.85px;
  height: 78.06px;
  background-size: cover;
  background-position: center;
}